import React, {useState} from 'react'
import ArtWorks from './ArtWorks';
import {Button} from 'react-bootstrap'

import './ContainerArtWorks.css'

const ContainerArtWorks = ({ artworks, children }) => {

  const [view, setView] = useState(false)
  const [currentPage, setcurrentPage] = useState(0)
  const [counter, setCounter] = useState(1)

  const nextPage = () => {
    setcurrentPage(currentPage + 8 )
    counterHandleMore()
  } 

  const backPage = () => {
    currentPage > 0 ? setcurrentPage(currentPage - 8 ) : alert('estas en la pagina 1')
    counterHandleLess()
  } 

  const counterHandleMore = () => {
    setCounter(counter+1)
  }
  const counterHandleLess = () => {
    setCounter(counter-1)
  }

  const viewHandler = () => {
    view === true ? setView(false) : setView(true)
  }
    const artWorksMapped = artworks.map((artwork) => (
      <ArtWorks
        key={artwork.id}
        title={artwork.title}
        img={artwork.img}
        id={artwork.id}
      />
    ));

    const filteredArtWorks = () => {
      return artWorksMapped.slice(currentPage, currentPage + 8)
    }

  return (

    <>
        {view === false ? (
          <section id="artworks"  className='default-container'>
          <h1 id="titleArtworks" className='titelMimimized'> VANGUARD CARDS </h1>
          <button className='button' onClick={viewHandler}>Open</button >
          </section>
        ) : (
          <div className='background-true'>
            <h1 className='titelMimimized'> VANGUARD CARDS </h1>
          <div>
          <div className="artowrk-container">
          {filteredArtWorks()}
          </div >
          <div className='default-container'>
            <div className='buttonsContainer'>

            <button disabled = {currentPage == 0} 
                    onClick={backPage} className='buttonPag'>
                      {"<< "}
            </button>
            <label className='counter-Pagination-Label'> {counter} </label>

            <button onClick={nextPage} className='buttonPag'>{" >>" }  </button>

            </div>
            <button className='button' onClick={viewHandler}>Close</button >
            </div>
          </div>
          </div>
        )}
    </>
      )
}

export default ContainerArtWorks